.allSections {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: url(../../images/bgcolor2.jpg);
  width: 100%;
  height: 100%;
  background-size: cover;
  @media all and (min-width: 768px) {
    height: 100vh;
  }

  .pSection {
    text-align: center;
    margin-top: 100px;
    color: rgb(197, 197, 197);
    font-family: 'Quicksand', sans-serif;
    font-size: 30px;
    .descriptionSection {
      padding-top: 20px;
      font-size: 17px;
    }
  }
  .arrow {
    justify-content: center;
    align-items: center;
    margin-top: 10px;
    max-width: 30px;
    max-height: 30px;
  }
  .projectImages {
    display: flex;
    flex-direction: column;
    align-items: center;
    .imageOne {
      max-width: 250px;
      max-height: 250px;
      margin-top: 30px;
      border: solid 2px #a598ec;
      border-radius: 6px;
      @media all and (min-width: 768px) {
        margin-top: 90px;
        max-width: 200px;
        max-height: 200px;
      }
      @media all and (min-width: 1024px) {
        margin-top: 60px;
        max-width: 250px;
        max-height: 250px;
      }
    }
    .pGraphic {
      text-align: center;
      font-size: 25px;
      font-family: 'Pacifico', cursive;
      color: #a598ec;
    }
    @media all and (min-width: 768px) {
      display: flex;
      flex-direction: row;
      align-items: center;
      text-align: start;
    }
  }
  .maquetationImg {
    padding: 10px;
  }
}
